
@font-face {
    font-family: "BloggerSansFont";
    src: url("../res/Blogger/BloggerSans.ttf") format("truetype"); }

@font-face {
    font-family: "BloggerSansFont";
    src: url("../res/Blogger/BloggerSans-Bold.ttf") format("truetype");
    font-weight: bold; }


@font-face {
    font-family: "BloggerSansFont";
    src: url("../res/Blogger/BloggerSans-BoldItalic.ttf") format("truetype");
    font-style: italic;
    font-weight: bold; }

@font-face {
    font-family: "BloggerSansFont";
    src: url("../res/Blogger/BloggerSans-Italic.ttf") format("truetype");
    font-style: italic; }

/*---------"BRAND" Colors---------"*/
@import "./colorShemes/dark.css";

$success_green: rgb(50, 214, 0);
$attention_red: rgb(255, 193, 193);
$warning_red: rgb(173, 0, 0);
$cursed_blue: rgb(0, 243, 255);
$emphasis_blue: rgb(15, 176, 255);
$post-yellow: rgb(228, 208, 0);

$eyeOfJudgement: rgb(255, 0, 254);

$upvote: rgb(31, 152, 229);
$downvote: rgb(255, 0, 79);
$novote: rgb(132, 132, 132);

/*----------- Global Styles -----------*/
body {
    background-color: var(--global_background);
    font-family: "BloggerSansFont";
    font-size: 21px;
    -webkit-text-size-adjust: 100%;
    margin: 0;
    padding: 0;
    color: var(--text_color); }


/*These ignore the font style from the body, take them*/
textarea, input, button, select, option {
    font-family: inherit;
    font-size: inherit; }


::selection {
    background-color: var(--selection); }


/*----------- Classes -----------*/
.container {
    margin-top: 55px;
    overflow: hidden;
    /*display: inline-block*/ }

.header {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;

    //background-image: linear-gradient(--header1, --header2) /* linear-gradient(#ff0024, #ffb300)  linear-gradient($warning_red, $downvote)*/
    background-color: var(--header-solid);
    color: #ffffff;
    margin-bottom: 20px;
    position: fixed;
    top: 0;
    z-index: 2;
    font-weight: bolder;

    * {
        margin: 10px;
        padding: 5px; }


    //divnpm run deploy

    //  //border-radius: 10px
    //div:hover
    //  //background-color: #ffffff80 */
    a, a:visited {
        color: inherit;
        text-decoration: none;
        border-radius: 10px; }

    a:hover {
        text-decoration: underline;
        background-color: #ffffff80; } }






/*rgb(7, 7, 34) post downvoted*/
/*rgb(34, 32, 7) postupvoted*/

/*rgb(60, 60, 60) for sleeping arrows maybe*/
.form {
    display: flex;
    flex-flow: column;

    button, input, textarea, select {
        width: 100%;

        border: 2px solid var(--border);
        border-radius: 10px;

        padding: 5px;

        background-color: var(--foreground);
        color: var(--text_color);

        font-size: inherit; }

    select {
        appearance: none;
        -webkit-appearance: none;
        background: url("/res/img/dropdownarrow.svg") no-repeat right var(--foreground);
        background-size: 20px;
        background-position: calc(100% - 10px);
        padding-right: 20px; }

    textarea {
        resize: vertical;
        rows: 10; }

    input[type="checkbox"] {
        -webkit-appearance: none;
        border-radius: 20px;
        height: 20px;
        width: 20px;

        &[required] {
            background-color: $attention_red; }
        &:checked {
            background-color: $success_green;
            box-shadow: 0 1px 2px rgba(0,0,0,0.05), inset 0px -15px 10px -12px rgba(0,0,0,0.05), inset 15px 10px -12px rgba(255,255,255,0.1); }

        &:active, &:checked:active {
            box-shadow: 0 1px 2px rgba(0,0,0,0.05), inset 0px 1px 3px rgba(0,0,0,0.1); } }

    input[type="submit"], button {
        width: 100%;

        &:hover {
            background-color: $post-yellow; } } }


/*-------- UTIL --------*/
.helpbox {
    background-color: $attention_red;
    color: rgb(46, 46, 46);
    position: absolute;
    border: 1px solid black;
    border-radius: 30px;
    padding: 10px;
    z-index: 100;
    display: none;

    a, a:visited {
        color: $warning_red;
        text-decoration: none; }

    a:hover {
        text-decoration: underline; } }


/* Generic */
.wordwrap {
    word-break: break-word;
    word-wrap: break-word;
    white-space: pre-wrap; }

.small {
    font-size: 18px; }


/* TEXT COLOR */
.red {
    color: darkred; }


/*SPOILER TAGS*/
.spoiler, .spoiler2 {
    color: black;
    background-color: black; }


.spoiler:hover {
    color: inherit; }


.spoiler2:hover {
    background-color: inherit;
    color: inherit; }


/*TRIIIIIIIIIIIGERED prefix mixin*/
#triggered {
    text-transform: uppercase;
    display: inline-block;
    font-weight: bold;
    animation: triggered 0.2s steps(2) infinite; }



@keyframes triggered {
    0% {
        transform: translate(2px,1px) rotate(0deg); }

    10% {
        transform: translate(-1px,-2px) rotate(-2deg); }

    20% {
        transform: translate(-3px,0) rotate(3deg); }

    30% {
        transform: translate(0,2px) rotate(0deg); }

    40% {
        transform: translate(1px,-1px) rotate(1deg); }

    50% {
        transform: translate(-1px,2px) rotate(-1deg); }

    60% {
        transform: translate(-3px,1px) rotate(0deg); }

    70% {
        transform: translate(2px,1px) rotate(-2deg); }

    80% {
        transform: translate(-1px,-1px) rotate(4deg); }

    90% {
        transform: translate(2px,2px) rotate(0deg); }

    100% {
        transform: translate(1px,-2px) rotate(-1deg); } }

